@media only screen and (max-width: 1450px) {

  .backroom_wrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: flex-start;
    align-content: flex-start;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
    background-color: #FFF;
    box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.25), 0 0 5px 0 rgba(0, 0, 0, 0.25), 0 0 5px 0 rgba(0, 0, 0, 0.25);
  }

  .main_window {
    width: 100%;
    background-color: #FFF;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
    flex-grow: 2;
    box-shadow: 0 0 2.5px 0 rgba(0, 0, 0, 0.25), 0 0 2.5px 0 rgba(0, 0, 0, 0.25), 0 0 2.5px 0 rgba(0, 0, 0, 0.25);
  }

  .RTV_main_window {
    width: 100%;
    background-color: #FFF;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
    flex-grow: 2;
    min-height: 70vh;
    box-shadow: 0 0 2.5px 0 rgba(0, 0, 0, 0.25), 0 0 2.5px 0 rgba(0, 0, 0, 0.25), 0 0 2.5px 0 rgba(0, 0, 0, 0.25);
  }

  .side_window {
    width: 100%;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
  }

  .side_bar ul {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: space-around;
    align-items: flex-start;
    align-content: flex-start;
  }

  .side_bar li {
    align-self: center;
    width:25%;
    font-size: 12pt;
  }

  .default_button {
    font-size: 10pt;
    margin: 5px;
    padding: 5px;
  }

  .DBedit_main_window {
    background-color: #dbdbdb;
    padding-top: 0px;
    padding-bottom: 0px;
    padding-left: 0px;
    padding-right: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
    flex-grow: 2;
    box-shadow: 0 0 2.5px 0 rgba(0, 0, 0, 0.25), 0 0 2.5px 0 rgba(0, 0, 0, 0.25), 0 0 2.5px 0 rgba(0, 0, 0, 0.25);
  }

  .DBedit_main_window h3 {
    text-align: center;
    background-image: linear-gradient(rgb(255, 255, 255, 1),  rgb(219, 219, 219, 1) );
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 0px;
    padding-right: 0px;
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 0px;
    margin-right: 0px;
    box-shadow: 0 0 2.5px 0 rgba(0, 0, 0, 0.25), 0 0 2.5px 0 rgba(0, 0, 0, 0.25), 0 0 2.5px 0 rgba(0, 0, 0, 0.25);
  }


}

@media only screen and (max-width: 1000px) {

  .default_header_link {
    font: 300 15pt 'Lato', sans-serif;
    padding-left: 15px;
    padding-right: 15px;
    text-decoration: none;
  }

}

@media only screen and (max-width: 800px) {

  .default_header_link {
    font: 300 12pt 'Lato', sans-serif;
    padding-left: 10px;
    padding-right: 10px;
    text-decoration: none;
  }

  .logo_rectangle{
    max-height: 30px;
  }

  .quick_play_container {
    width: 100%;
    min-width: 100%;
    max-width: 100%;
  }

  .other_play_container {
    width: 100%;
    min-width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 0 auto 0 auto;
    animation: fade_in .3s ease-in-out 1, blur_in .15s ease-in-out 1;
  }

  .quick_play_button {
    width: 75%;
    min-width: 75%;
    max-width: 75%;
  }

  .play_by_difficulty_button {
    width: 75%;
    min-width: 75%;
    max-width: 75%;
  }

  .play_by_category_button {
    width: 75%;
    min-width: 75%;
    max-width: 75%;
  }

}

@media only screen and (max-width: 750px) {

  .default_wrapper{
    min-height: 75vh;
  }

  .select_buttons_container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
  }

  .RTV_main_window{
    min-height: 75vh;
  }

  .main_window {
    width: 100%;
    flex-grow: 1;
    flex-shrink: 2;
    z-index: 1;
    min-height: 70vh;
  }

  .side_bar li {
    font-size: 10pt;
    padding-left: 5px;
    padding-right: 5px;
  }

  .DBedit_navbar_item p {
    font-size: 8pt;
    text-align: center;
    padding: 8px;
    margin-block-start: 0rem;
    margin-block-end: 0rem;
  }

  .RTVusers_table th {
    font-size: 10pt;
    padding: 5px;
  }

  .RTVusers_table tr {
    padding: 5px;
  }

  .RTVusers_table td {
    font-size: 8pt;
    padding: 5px;
  }

  .RTVpages_table th {
    font-size: 10pt;
    padding: 5px;
  }

  .RTVpages_table tr {
    padding: 5px;
  }

  .RTVpages_table td {
    font-size: 8pt;
    padding: 5px;
  }

  .div1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
  }

  .div2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
  }

}

@media only screen and (max-width: 700px) {

  .header_left {
    flex-shrink: 2;
  }

  .header_left a img {
    flex-shrink: 2;
  }

  .header_right {
    flex-direction: column;
    width:100%;
    flex-wrap: wrap;
    padding-right: 0px;
    align-items: flex-end;
  }

  .header_greeting {
    margin-top: 10px;
    margin-right: -20px;
    align-self: flex-end;
    font: 300 8pt 'Lato', sans-serif;
    order: 1;
  }

  .links a{
    font-size: 9pt;
    margin: 0px auto 0px auto;
    text-decoration: none;
  }

  .default_header_link {
    font: 400 10pt 'Lato', sans-serif;
    padding-bottom: 10px;
    text-decoration: none;
  }

  .logos {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    align-content: center;
    font-size: 10pt;
    margin: 15px 10px 15px 0px;
  }

  .logo_rectangle{
    max-width: 100%;
    height: auto;
    max-height: 20px;
    margin: 5px;
  }

  .dashboard_tabs ul {
    display: block;
  }

  .dashboard_tabs li {
    padding: 5px;
  }

  .stats_difficulty {
    display: block;
  }

  .stats_category ul {
    max-width: 100%;
  }

  .question_card_text h2 {
    padding: 15px 10px 15px 10px;
  }

  .div1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
  }

  .div2 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
  }

  .question_card_all_comments li p{
    font: 300 10pt 'Lato', sans-serif;
  }

}

@media only screen and (max-width: 500px) {

  .logo_rectangle{
    max-height: 20px;
  }

  .question_card_text h2 {
    padding: 15px 10px 15px 10px;
  }

}