
.loading_container {
  width: 100wh !important;
  min-height: 50vh !important;
  margin: 0px auto 0px auto;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
}

.loading_header {
  width: 100%;
  padding: 10px 0px 10px 0px;
  animation: loading_pulse 2s linear infinite;
  z-index:8;
}

.loading_header h3 {
  font: 300 25pt 'Lato', sans-serif !important;
}

.loading_animation_container {
  margin: 0px auto 0px auto !important;
  animation: loading_pulse 1.2s ease-in-out infinite !important;
}

/* <div class="lds-ring"><div></div><div></div><div></div><div></div></div> */

.lds-ring {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  }
  .lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid rgba(191, 191, 191, 1);
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: rgba(191, 191, 191, 1) transparent transparent transparent;
  }
  .lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
  }
  .lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
  }
  .lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
  }
  @keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
  }

/* <div class="lds-dual-ring"></div> */

  .lds-dual-ring {
  display: inline-block;
  width: 80px;
  height: 80px;
  }
  .lds-dual-ring:after {
  content: " ";
  display: block;
  width: 64px;
  height: 64px;
  margin: 8px;
  border-radius: 50%;
  border: 6px solid rgba(191, 191, 191, 1);
  border-color: rgba(191, 191, 191, 1) transparent rgba(191, 191, 191, 1) transparent;
  animation: lds-dual-ring 1.2s linear infinite;
  }
  @keyframes lds-dual-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
  }

/* <div class="lds-hourglass"></div> */

  .lds-hourglass {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
  }
  .lds-hourglass:after {
  content: " ";
  display: block;
  border-radius: 50%;
  width: 0;
  height: 0;
  margin: 8px;
  box-sizing: border-box;
  border: 32px solid rgba(191, 191, 191, 1);
  border-color: rgba(191, 191, 191, 1) transparent rgba(191, 191, 191, 1) transparent;
  animation: lds-hourglass 1.2s infinite;
  }
  @keyframes lds-hourglass {
  0% {
    transform: rotate(0);
    animation-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  50% {
    transform: rotate(900deg);
    animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  100% {
    transform: rotate(1800deg);
  }
  }

/* <div class="lds-roller"><div></div><div></div><div></div><div></div><div></div><div></div><div></div><div></div></div> */

.lds-roller {
  display: inline-block;
  position: relative;
  width: 100px;
  height: 100px;
  }
  .lds-roller div {
  animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  transform-origin: 40px 40px;
  }
  .lds-roller div:after {
  content: " ";
  display: block;
  position: absolute;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: rgba(102, 102, 102, 1);
  margin: -4px 0 0 -4px;
  }
  .lds-roller div:nth-child(1) {
  animation-delay: -0.036s;
  }
  .lds-roller div:nth-child(1):after {
  top: 63px;
  left: 63px;
  }
  .lds-roller div:nth-child(2) {
  animation-delay: -0.072s;
  }
  .lds-roller div:nth-child(2):after {
  top: 68px;
  left: 56px;
  }
  .lds-roller div:nth-child(3) {
  animation-delay: -0.108s;
  }
  .lds-roller div:nth-child(3):after {
  top: 71px;
  left: 48px;
  }
  .lds-roller div:nth-child(4) {
  animation-delay: -0.144s;
  }
  .lds-roller div:nth-child(4):after {
  top: 72px;
  left: 40px;
  }
  .lds-roller div:nth-child(5) {
  animation-delay: -0.18s;
  }
  .lds-roller div:nth-child(5):after {
  top: 71px;
  left: 32px;
  }
  .lds-roller div:nth-child(6) {
  animation-delay: -0.216s;
  }
  .lds-roller div:nth-child(6):after {
  top: 68px;
  left: 24px;
  }
  .lds-roller div:nth-child(7) {
  animation-delay: -0.252s;
  }
  .lds-roller div:nth-child(7):after {
  top: 63px;
  left: 17px;
  }
  .lds-roller div:nth-child(8) {
  animation-delay: -0.288s;
  }
  .lds-roller div:nth-child(8):after {
  top: 56px;
  left: 12px;
  }
  @keyframes lds-roller {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
  }