select:not(:-internal-list-box) {
  overflow: hidden !important;
}

.edit_profile_form {
  width: 100%;
  text-align: center;
  margin: 0 auto 0px auto;
  border: 1px solid rgb(200, 200, 200, 1);
  box-sizing: border-box;
  animation: fade_in .25s ease-in-out 1, blur_in .25s ease-in-out 1, slide_in_from_bottom.25s ease-in-out 1;
}

.edit_profile_form label {
  animation: fade_in .4s ease-in-out 1, blur_in .2s ease-in-out 1;
}

.edit_div {
  margin: 10px 0px 10px 0px;
}

.edit_div > * {
  font: 400 12px "Helvetica", sans-serif;
  padding: 10px;
  width: 50%;
  max-width: 350px;
  line-height: 1.25;
  border: 1px solid rgb(200, 200, 200, 1);
  box-sizing: border-box;
  animation: fade_in .4s ease-in-out 1, blur_in .2s ease-in-out 1 scale_in .1 ease-in-out 1;
}

.edit_div label {
  font: 300 10pt 'Poppins', sans-serif;
  border: 0px;
}

.edit_buttons_container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: baseline;
  margin: 20px auto 20px auto;
  border: 1px solid rgb(200, 200, 200, 1);
  box-sizing: border-box;
  animation: fade_in .3s ease-in-out 1, blur_in .15s ease-in-out 1;
}
